@charset "UTF-8";
@import "../../../assets/theme/default/var.css";

@component-namespace template{
  @component table {
    width:100%;
   /* margin-top:20px;*/
    @descendent bar{
      margin-top:20px;
    }
    /*@descendent search{
      padding: 15px;
      background-color: #f2f2f2;
      float: left;
      width:100%;
    }*/
    @descendent filter{
      padding: 15px;
      background-color: #f1f1f1;
      float: left;
      width:100%;
      margin-bottom: 15px;
    }
    @descendent plat {
      position: relative;
    }
    @descendent control{
       position: absolute;
       top: 50%;
       right:-140px;
       margin-top: -36px;
     }
    @descendent result{
      padding: 15px;
      background-color: #f2f2f2;
      float: left;
      width:100%;
    }
    @descendent pagination{
      float:right;
      margin-top:20px;
    }
  }
}
.text-center{
  text-align: center;
}
.rule .el-tag{
  margin-right:4px;
}
.control{
  text-align: center;
  margin-top:15px;
}
.cell .el-dropdown-link{
  cursor: pointer;
  color:var(--color-primary);
}
.template-table-control{
  position: absolute;
  top: 50%;
  right:-140px;
  margin-top: -18px;
}
.el-dropdown-menu{
  width:30px;

}



